/**
 * @file  newsList
 * @author sfe
 */

import axios from 'axios';

export default {
    async getNewsList(params) {
        let data = await axios('https://pwa.baidu.com/api/mockup/news/' + params.category, {
            withCredentials: true
        });

        if (params.nid) {
            let news = data.data.data.news || [];
            data.data.data.news = news.filter(item => item.nid === params.nid) || news[0] || [];
        }

        return data.data.data;
    },

    getUserInfo(userId = '2459115') {
        return new Promise(resolve => {
            resolve({
                userName: 'Baidu',
                messageCount: 10
            });
        });
    },

    getHotNews() {
        return new Promise(resolve => {
            resolve({
                news: [
                    {
                        title: '',
                        abs: '...',
                        ts: '1496806231000',
                        url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                    },
                    {
                        title: '',
                        abs: '...',
                        ts: '1496806231000',
                        url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                    },
                    {
                        title: '',
                        abs: '...',
                        ts: '1496806231000',
                        url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                    }
                ]
            });
        });
    },
    searchNews(query) {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve({
                    news: [
                        {
                            title: '',
                            abs: '...',
                            ts: '1496806231000',
                            url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                        },
                        {
                            title: '',
                            abs: '...',
                            ts: '1496806231000',
                            url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                        },
                        {
                            title: '',
                            abs: '...',
                            ts: '1496806231000',
                            url: 'http://www.sznews.com/news/content/2016-12/18/content_14606309.htm'
                        }
                    ]
                });
            }, 1500);
        });
    }

};



